import axios from 'axios';
import MockAdapter from 'axios-mock-adapter';

import { queryParamsGetAll } from '../../../helpers';

import setup from './data/product-list-setup.json';

import data from './data/product-list.json';
import dataActive from './data/product-list-active.json';
import dataPaused from './data/product-list-paused.json';
import dataSearch from './data/product-list-search.json';
import dataEmpty from './data/product-list-empty.json';
import dataPaginate from './data/product-list-paginate.json';

import responseDelete from './data/action-delete-success.json';
import responsePause from './data/action-pause-success.json';
import responsePublish from './data/action-publish-success.json';

// Mock api
const apiSearch = '/search';
const apiPublish = '/publish';
const apiPause = '/pause';
const apiDelete = '/delete';
const apiError = '/error';

const params = queryParamsGetAll();

// console.log('ALL PARAMS', params);

const apiMock = () => {
    const mock = new MockAdapter(axios, { delayResponse: 300 });

    mock.onAny(apiError).reply(404);

    mock.onAny(apiSearch, {
        params: {
            ...params,
            keyword: 'search',
            status: 'all',
            page: '1',
        },
    }).reply(200, { ...dataSearch });

    mock.onAny(apiSearch, {
        params: {
            ...params,
            keyword: 'empty',
            status: 'all',
            page: '1',
        },
    }).reply(200, { ...dataEmpty });

    mock.onAny(apiSearch, {
        params: {
            ...params,
            status: 'active',
            page: '1',
        },
    }).reply(200, { ...dataActive });

    mock.onAny(apiSearch, {
        params: {
            ...params,
            status: 'paused',
            page: '1',
        },
    }).reply(200, { ...dataPaused });

    mock.onAny(apiSearch, {
        params: {
            ...params,
            status: 'all',
            page: '2',
        },
    }).reply(200, { ...dataPaginate });

    mock.onAny(apiSearch).reply(200, { ...data });

    mock.onAny(apiPause).reply(200, { ...responsePause });

    mock.onAny(apiPublish).reply(200, { ...responsePublish });

    mock.onAny(apiDelete).reply(200, { ...responseDelete });

    return mock;
};

// TODO: This is imported by ProductList.test - not sure what's expected here??
const config = {};

export { apiMock, setup, apiSearch, apiPublish, apiPause, apiDelete, apiError, config };
